import { Select } from '@chakra-ui/react';
import styled from '@emotion/styled';
import { useEffect } from 'react';

import { updateBaseURL } from '@/api/instance';
import { Image } from '@/components/common/Image';
import { Container as CommonContainer } from '@/components/common/layouts/Container';
import { breakpoints } from '@/styles/variants';

export const SelectFriendsBanner = () => {
  const handleBaseURL = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value;
    localStorage.setItem('environment', value);
    updateBaseURL(value);
  };

  useEffect(() => {
    const handleStorageChange = () => {
      const environment = localStorage.getItem('environment');
      if (environment) {
        updateBaseURL(environment);
      }
    };

    //    localStorage    
    handleStorageChange();

    //    
    window.addEventListener('storage', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []);

  return (
    <Wrapper>
      <Container flexDirection="row" alignItems="center">
        <SelectImage
          src="https://gift-s.kakaocdn.net/dn/gift/images/m640/bg_profile_default.png"
          alt="   "
          onClick={() => {
            alert('   ');
          }}
        />
        <Text>   .</Text>
      </Container>
      <ApiSelection placeholder="BASE_URL " onChange={handleBaseURL}>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </ApiSelection>
    </Wrapper>
  );
};

const Wrapper = styled.section`
  width: 100%;
  height: 76px;
  padding: 18px 16px;

  background: #fafafa;

  @media screen and (min-width: ${breakpoints.sm}) {
    height: 150px;
    padding: 40px 16px;
  }
`;

const Container = styled(CommonContainer)`
  align-content: space-between;
  height: 100%;
`;

const SelectImage = styled(Image)`
  width: 40px;
  height: 40px;
  border-radius: 16px;
  cursor: pointer;

  @media screen and (min-width: ${breakpoints.sm}) {
    width: 70px;
    height: 70px;
    border-radius: 24px;
  }
`;

const Text = styled.p`
  padding-left: 16px;
  font-size: 17px;
  line-height: 22px;
  color: #333;
  font-weight: 500;

  @media screen and (min-width: ${breakpoints.sm}) {
    font-size: 28px;
    line-height: 35px;
  }
`;

const ApiSelection = styled(Select)`
  margin: 10px;
`;
